import * as React from 'react';
import Box from '@mui/material/Box';
import CssBaseline from '@mui/material/CssBaseline';
import BottomNavigation from '@mui/material/BottomNavigation';
import BottomNavigationAction from '@mui/material/BottomNavigationAction';
import RestoreIcon from '@mui/icons-material/Restore';
import FavoriteIcon from '@mui/icons-material/Favorite';
import ArchiveIcon from '@mui/icons-material/Archive';
import AttachFileIcon from '@mui/icons-material/AttachFile';
import Paper from '@mui/material/Paper';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemAvatar from '@mui/material/ListItemAvatar';
import ListItemText from '@mui/material/ListItemText';
import Avatar from '@mui/material/Avatar';
import TextField from '@mui/material/TextField';
import DirectionsIcon from '@mui/icons-material/Directions';
import Divider from '@mui/material/Divider';
import IconButton from '@mui/material/IconButton';
import SendIcon from '@mui/icons-material/Send';
import Typography from '@mui/material/Typography';
import { useFileUpload } from 'use-file-upload'
import ImageList from '@mui/material/ImageList';
import ImageListItem from '@mui/material/ImageListItem';
import DownloadIcon from '@mui/icons-material/Download';
import { Link } from 'react-router-dom'



// function refreshMessages() {
//     const getRandomInt = (max) => Math.floor(Math.random() * Math.floor(max));
//     return Array.from(new Array(10)).map(
//         () => messageExamples[getRandomInt(messageExamples.length)],
//     );
// }

export default function ChatTextBox({ sendSocketHandler, messagesData, role = 'user', selectFileHandler, files, downlodeHandler }) {
    const [value, setValue] = React.useState('');
    const ref = React.useRef(null);
    const [messages, setMessages] = React.useState([]);

    React.useEffect(() => {
        setMessages(messagesData);
    }, [messagesData]);


    return (
        <Box>
            <Box sx={{ pb: 7, height: "60vh", overflow: 'auto' }} ref={ref}>
                <CssBaseline />
                <List >
                    {messages.map((data, index) => {
                        if (data.type !== role) {
                            return (
                                <React.Fragment key={index + 1}>

                                    <ListItem button sx={{ textAlign: 'left' }}>
                                        <ListItemAvatar>
                                            <Avatar alt="Profile Picture" src={"person"} />
                                        </ListItemAvatar>
                                        <ListItemText primary={data.name} secondary={data.text} />
                                    </ListItem>
                                    <ListItem>
                                        {data.file && <img src={data.file} style={{ width: "50%", float: "right" }} alt="testImage" />}
                                    </ListItem>
                                </React.Fragment>
                            )
                        } else {
                            return (
                                <React.Fragment key={index + 1}>
                                    <ListItem button >
                                        <ListItemText primary={<Typography align='right' >
                                            {data.name}
                                        </Typography>}
                                            secondary={
                                                <Typography align='right' sx={{ color: 'gray', fontSize: 15 }}>
                                                    {data.text}
                                                </Typography>
                                            } />
                                        <ListItemAvatar sx={{ paddingLeft: 2 }} >
                                            <Avatar alt="Profile Picture" src={'person'} />
                                        </ListItemAvatar>
                                    </ListItem>
                                    <ListItem sx={{ display: 'inline-block' }}>

                                        {data.file &&
                                            data.file.length ? (data.file).map(ele => (
                                                <>
                                                    {ele.split('.').pop() == ('jpeg' || 'png' || 'webp')
                                                        ? <img src={ele} style={{ width: "50%", float: "right" }} alt="testImage" />
                                                        : <p style={{ textAlign: 'right' }} >{ele.substr(ele.lastIndexOf('/') + 1)}</p>}
                                                    <Link to={ele} target="_blank" download>
                                                        <IconButton style={{ float: 'right' }} onClick={() => downlodeHandler(ele.substr(ele.lastIndexOf('/') + 1))}>
                                                            <DownloadIcon />
                                                        </IconButton>
                                                    </Link>
                                                </>
                                            )) : ''

                                        }
                                    </ListItem>
                                </React.Fragment>
                            )
                        }
                    }
                    )}
                </List>
            </Box>
            <Paper sx={{ p: '2px 4px', display: 'flex', alignItems: 'center' }} elevation={3}>
                <IconButton color="primary" onClick={selectFileHandler} sx={{ p: '10px' }} aria-label="directions">
                    <AttachFileIcon />
                </IconButton>
                <Divider sx={{ height: 40, m: 1 }} orientation="vertical" />

                <TextField
                    id="outlined-multiline-flexible"
                    placeholder='Type your messge here...'
                    multiline
                    fullWidth
                    maxRows={4}
                    value={value}
                    onChange={(e) => setValue(e.target.value)}
                />
                <Divider sx={{ height: 40, m: 1 }} orientation="vertical" />
                <IconButton color="primary" onClick={() => { sendSocketHandler(value), setValue('') }} sx={{ p: '10px' }} aria-label="directions">
                    <SendIcon />
                </IconButton>
            </Paper>

        </Box>
    );
}
