import * as React from 'react';
import TreeView from '@mui/lab/TreeView';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import ChevronRightIcon from '@mui/icons-material/ChevronRight';
import TreeItem from '@mui/lab/TreeItem';

export default function MultiSelectTreeView({data,handleChangeTree}) {

  return (
    <TreeView
      aria-label="multi-select"
      defaultCollapseIcon={<ExpandMoreIcon />}
      defaultExpandIcon={<ChevronRightIcon />}
      sx={{ height: 216, flexGrow: 1, maxWidth: 400, overflowY: 'auto', }}
    >
        {data.map(ele=>(
            <TreeItem key={ele._id} nodeId={ele._id} label={ele.department} sx={{paddingRight:'auto',}} id={ele._id}  onClick={()=>handleChangeTree(ele._id)}>
                {(ele.list).map((elem,i)=>(
                <TreeItem id={ele._id} key={i} label={elem} vlaue={ele._id} title={ele._id} />
              ))}
            </TreeItem>
        ))}
    </TreeView>
  );
}