import * as React from 'react';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import Box from '@mui/material/Box';
import FormHelperText from '@mui/material/FormHelperText';



export default function SelectVariants({data=[],inputProps,labelText,id,helperText,error}) {
   
  return (
    <div>
    <Box sx={{marginTop:2}}>

      <FormControl  sx={{ minWidth: '100%'}} error={error}>
        <InputLabel id={id}>{labelText}</InputLabel>
        <Select
          labelId={id}
          id={id}
          {...inputProps}
        >
          {data.map((ele,i)=>(
              <MenuItem key={i} value={ele}>{ele}</MenuItem>
          ))}
        </Select>
      {helperText ? <FormHelperText error={error} id={id}>{helperText}</FormHelperText> : ''}
      </FormControl>
      </Box>
      </div>
  );
}