import * as React from 'react';
import moment from 'moment';

import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';


export default function BasicTable({data={name: '',email:'',}}) {
     const dataTemp = {
        
            Name: `${data.title}. ${data.name}`,
            Email: data.email,
            "Father's Name":`Mr. ${data.father}`,
            'Date of Birth': moment(data.dob).format('MMM DD, YYYY'), 
            Mobile: data.mobileNo,
            Address: data.address,
            Position: data.position,
            Department: data.department,
            'Joining Date': moment(data.joinDate).format('MMM DD, YYYY'),
            salary: data.salary,
            Status: data.status,
            ...(data.formerDate && {'Former Date': moment(data.joinDate).format('MMM DD, YYYY')})

            
        
     }
    const keys = Object.keys(dataTemp);
    
  return (
    <TableContainer component={Paper}>
      <Table sx={{ minWidth: 200 }} aria-label="simple table">
        <TableBody>
            
                {keys.map((ele,i)=>(
                <TableRow key={i}
                    sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                  >
                    <TableCell component="th" scope="row">
                    {ele}
                  </TableCell>
                  <TableCell align="right">{dataTemp[ele]}</TableCell>
            </TableRow>
                ))}
              
        </TableBody>
      </Table>
    </TableContainer>
  );
}
